#root {
  width: 100%;
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  height: 100vh;
  font-family: 'Gabriela', Arial, serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #EEE;
}

.header {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: left;
  width: 100%;
  height: 70px;
  border-bottom: #c7c7c7 1px solid;
}
  
.header-search-bar {  
  width: 210px;
}

.mainfrapper {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  height: 100%;
}
.wrapper {
  max-width: 1600px;
  margin: 0 auto;
  height: 100%; }

.footer {
  text-align: center;
  width: 100%;
  height: 40px;
  background-color: #040404;
  color: #e6e6e6;
  border-top: solid 1px;
  display: flex;
  justify-content: center;
  align-items: center; }

.dashboard {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center; }

.dashboard-table {
  margin: 20px 0;
  width: 80%;
  border-radius: 10px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
  padding: 8px;
  background-color: #fcfaf9; }

.dashboard-cell {
  text-align: center;
  padding: 10px 0; }

.dashboard-status-red {
  background-color: #b5febd; }

.dashboard-status-green {
  background-color: #3de93d; }

.dashboard-status-grey {
  background-color: #e5e5e5; }

.dashboard-status-yellow {
  background-color: #fffa60; }

.header {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: left;
  width: 100%;
  height: 70px;
  border-bottom: #c7c7c7 1px solid; }

.header-search-bar {
  width: 210px; }

